import { audioPath } from "./paths";

let audio = new Audio();
audio.onended = () => console.log("selesai");

async function playMurottal(folder, track) {
  console.log("play");
  audio.src = audioPath + folder + "/" + track;
  audio.play();
}

function stopMurottal() {
  console.log("stop");
  audio.pause();
}

export { playMurottal, stopMurottal };
